<template>
    <div class="message">
        <p>{{typedText}}</p>
    </div>
</template>

<script>
import { note } from "@/baragouin";

export default {
  name: "Message",

  props: {
    text: String
  },

  data() {
    return {
      typedText: "",
      speed: 30
    };
  },

  watch: {
    text() {
      if (this.shutup) this.shutup();
      this.typedText = "";
      this.shutup = this.machinetalk(this.text);
    }
  },

  created() {
    this.shutup = this.machinetalk(this.text);
  },

  destroyed() {
    this.shutup();
  },

  methods: {
    machinetalk(text) {
      let parts = text.split(/\b/);
      let t = 0;
      let tone = 100;
      let coefSpeed = 1 - this.speed / 120;
      let timeouts = [];

      for (let p of parts) {
        if (/\w/.test(p)) {
          let nbSyllabs = Math.ceil(p.length / 3);
          for (let s = 0; s < nbSyllabs; s++) {
            t += 100 * coefSpeed;

            let freq = 440 + tone;
            timeouts.push(setTimeout(() => note(freq, 0.5 * coefSpeed), t));
            timeouts.push(
              setTimeout(() => {
                this.typedText += p.slice(s * 3, s * 3 + 3);
              }, t)
            );
          }
        } else {
          timeouts.push(
            setTimeout(() => {
              this.typedText += p;
            }, t + 1)
          );
          switch (p.trim()) {
            case "":
              t += 50 * coefSpeed;
              tone = 90;
              break;
            case ",":
            case ";":
              t += 400 * coefSpeed;
              tone = 100;
              break;
            case ".":
            case "?":
            case "!":
            case "\n":
              t += 800 * coefSpeed;
              break;
            case "...":
              t += 1200 * coefSpeed;
              break;
            default:
              break;
          }
        }
      }

      setTimeout(() => {
        this.$emit("endmessage");
      }, t + 1000);

      return () => {
        timeouts.forEach(t => clearTimeout(t));
      };
    }
  }
};
</script>

<style lang="postcss" scoped>
.message {
  text-align: left;
}
</style>
